import React from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import { Grid } from "@material-ui/core";

import Local from "@cvccorp-components/icons/dist/Local";
import Calendar from "@cvccorp-components/icons/dist/Calendar";
import User from "@cvccorp-components/icons/dist/User";
import ArrowLeft from "@cvccorp-components/icons/dist/ArrowLeft";
import Button from "../Button";
import Typography from "../Typography";
import { styles } from "./styles";

const uuidv4 = require("uuid/v4");
const TopResume = ({ classes, destination, withModal, onBack }) => {
    return (
        <Grid container>
            <Grid item className={classes.researchContentWrapper} xs={12}>
                {destination.map(item => (
                    <span key={uuidv4()} className={classes.researchContent}>
                        {withModal ? (
                            <a onClick={onBack} className={classes.icon}>
                                <ArrowLeft />
                            </a>
                        ) : (
                            <span className={classes.researchContentAddressContainer}>
                                <span className={classes.researchContentAddressFull}>
                                    <span className={classes.researchIcon}>
                                        <Local size="large" />
                                    </span>
                                    <span className={classes.researchContentAddress}>
                                        <Typography
                                            className={classes.researchCity}
                                            lg="h4"
                                            md="h4"
                                            sm="h4"
                                        >
                                            {item.city}
                                        </Typography>
                                        <Typography
                                            className={classes.researchAddress}
                                            lg="body3"
                                            md="body3"
                                            sm="body3"
                                        >
                                            {item.state},{item.country}
                                        </Typography>
                                    </span>
                                </span>
                            </span>
                        )}
                        <Typography
                            className={classes.researchDate}
                            lg="body2"
                            md="body2"
                            sm="body2"
                        >
                            {!withModal && <Calendar />}
                            {item.departure} -{item.arrival}
                        </Typography>
                        <Typography
                            className={classes.researchPax}
                            lg="body2"
                            md="body2"
                            sm="body2"
                        >
                            <User size="small" className={classes.icon} />
                            {item.pax}
                        </Typography>
                    </span>
                ))}
                {!withModal && (
                    <Button
                        className={classes.researchBtn}
                        color="secondary"
                        label="Alterar busca"
                        variant="outlined"
                        size="small"
                    />
                )}
            </Grid>
        </Grid>
    );
};

TopResume.propTypes = {
    classes: PropTypes.object.isRequired,
    destination: PropTypes.array,
    withModal: PropTypes.bool,
    onBack: PropTypes.func,
};

export default withStyles(styles)(TopResume);
